	module.exports = {"data":{"markdownRemark":{"html":"<p>第二世代の東方Project 3作はいろいろあった。紅魔郷まではさほど知られていなかったと聞くが、永夜抄の行列を見て他人事ながら感動した。次なる世代の東方には、どんなびっくりが待ち受けるのか── 赤は桜、青は妖霧、そして紫が去らぬ夜をイメージしています。それらを突破した霊夢の目には一体何が映っているのだろうか。  ちなみに、現在製作中の弾幕風ステージスクリプト“<a href=\"!/thA/\">東方時封城 ～ the Alternative Age</a>”のイメージ画像として作りました。エンディングっぽい雰囲気だけど、タイトル画面あたりで使おうかな。 背景は3Dですが、霊夢は何もないところからPixiaで3Dっぽく描き上げました。彩色もPixiaで。Photoshop等有名な画像処理ソフトと比べると遥かに低機能(フリーとしては極めて高機能だが)ですが、使いやすさで比較するならPixiaに軍配が上がると思います。実際私もPhotoshop使用経験はありますが、余りに使いづらく結局ポイした人間なので。</p>","frontmatter":{"cover":null,"date":"2004-10-11T18:13:00+09:00","strDate":"2004/10/11","title":"[東方] 道・そして第三世代へ"}}},"pathContext":{"path":"/ja/blog/2004/10/11-東方-道・そして第三世代へ/","langKey":"ja"}}

